import styled, { createGlobalStyle, keyframes } from 'styled-components'

export const GlobalStyle = createGlobalStyle`
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
  body {
    box-sizing:border-box;
    font-family: 'Roboto', sans-serif;
    height: 100%;
  }
`

export const Container = styled.div`
  padding: 10px;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  min-width: 300px;
  max-width: 1200px;
`

export const CharactersList = styled.div`
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
`

export const CharactersElement = styled.div`
  min-width: 300px;
  max-width: 500px;
  margin: 10px;
  padding: 20px 10px;
  border: 3px solid green;
  border-radius: 10px;
  cursor: pointer;
`

export const StyledCharactersProp = styled.div`
  margin: 10px 0;
`
export const StyledPreloaderContainer = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`
export const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
`
export const StyledModal = styled.div`
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  z-index: 1000;
  border-radius: 30px;
  padding: 40px 30px 20px 30px;
`

const spin = keyframes`
0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
`

export const Spinner = styled.div`
  position: fixed;
  top: 50%;
  left: 50%;
  display: inline-block;
  width: 57px;
  height: 57px;
  z-index: 999;
  &:after {
    content: ' ';
    display: block;
    width: 30px;
    height: 30px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #15b345;
    border-color: #15b345 transparent #15b345 transparent;
    animation: ${spin} 1.2s linear infinite;
  }
`

export const StyledCard = styled.div`
  width: 300px;
  min-height: 400px;
`
export const StyledCrossButton = styled.button`
  overflow: hidden;
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  padding: 0;
  width: 2em;
  height: 2em;
  border-radius: 50%;
  background: transparent;
  color: #red;
  font: inherit;
  text-indent: 100%;
  cursor: pointer;

  &:focus {
    outline: solid 0 transparent;
    box-shadow: 0 0 0 2px #8ed0f9;
  }

  &:hover {
    background: rgba(29, 161, 142, 0.2);
  }

  &:before,
  &:after {
    position: absolute;
    top: 15%;
    left: calc(50% - 0.0625em);
    width: 0.125em;
    height: 70%;
    border-radius: 0.125em;
    transform: rotate(45deg);
    background: currentcolor;
    content: '';
  }

  &:after {
    transform: rotate(-45deg);
  }
`

export const StyledForm = styled.form`
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10px;
`

export const StyledLabel = styled.label`
  margin: 5px 10px;
`

export const StyledInput = styled.input`
  margin: 5px 10px;
  height: 25px;
  padding: 3px;
`

export const StyledSelect = styled.select`
  margin: 5px 10px;
  height: 34px;
`

export const StyledPaginator = styled.div`
  padding: 10px;
  margin: 20px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
`
export const StyledPage = styled.span`
  padding: 6px;
  text-align: center;
  margin: 3px 8px;
  border: solid 1px green;
  border-radius: 3px;
  width: 1rem;
  cursor: ${({ current } = { current: false }) =>
    current ? 'auto' : 'pointer'};
  color: ${({ current } = { current: false }) => (current ? 'white' : 'green')};
  background: ${({ current } = { current: false }) =>
    current ? 'green' : 'white'};
`
