import { createPagesArray } from './createPagesArray'
import { StyledPaginator, StyledPage } from '../StyledElements/StyledElements'

type PaginatorProps = {
  current: number
  pages: number
  onClick: (page: number) => void
}

export const Paginator: React.FC<PaginatorProps> = ({
  current = 1,
  pages,
  onClick,
}) => {
  const pagesList = createPagesArray(pages)

  return (
    <StyledPaginator>
      {pagesList.map((page) => {
        return page === current ? (
          <StyledPage current key={page}>
            {page}
          </StyledPage>
        ) : (
          <StyledPage key={page} role="button" onClick={() => onClick(page)}>
            {page}
          </StyledPage>
        )
      })}
    </StyledPaginator>
  )
}
