import { Dispatch, SetStateAction, useEffect, useState, useRef } from 'react'
import {
  StyledForm,
  StyledLabel,
  StyledInput,
  StyledSelect,
} from '../StyledElements/StyledElements'

type FilterProps = {
  setFilterParams: Dispatch<SetStateAction<RequestParams>>
}

export const Filter: React.FC<FilterProps> = ({ setFilterParams }) => {
  const firstRenderRef = useRef(true)
  const [internalFormState, setInternalFormState] = useState({})

  const onInputValueChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    setInternalFormState((oldFormState) => {
      return {
        ...oldFormState,
        [event.target.name]: event.target.value,
      }
    })
  }

  useEffect(() => {
    //skip in first render to avoid duplicate requests
    if (!firstRenderRef.current) {
      //debounce
      const timer = setTimeout(() => {
        setFilterParams(() => ({ ...internalFormState, page: 1 }))
      }, 1000)

      return () => clearTimeout(timer)
    }

    firstRenderRef.current = false
  }, [internalFormState, setFilterParams])

  return (
    <StyledForm>
      <StyledLabel>
        Name:
        <StyledInput
          type="text"
          name="name"
          value={internalFormState?.name || ''}
          onChange={onInputValueChange}
        />
      </StyledLabel>
      <StyledLabel>
        Species:
        <StyledInput
          type="text"
          name="species"
          value={internalFormState?.species || ''}
          onChange={onInputValueChange}
        />
      </StyledLabel>

      <StyledLabel>
        Type:
        <StyledInput
          type="text"
          name="type"
          value={internalFormState?.type || ''}
          onChange={onInputValueChange}
        />
      </StyledLabel>

      <StyledLabel>
        Status:
        <StyledSelect
          name="status"
          value={internalFormState?.status || ''}
          onChange={onInputValueChange}
        >
          <option value=""></option>
          <option value="Alive">Alive</option>
          <option value="Dead">Dead</option>
          <option value="unknown">unknown</option>
        </StyledSelect>
      </StyledLabel>

      <StyledLabel>
        Gender:
        <StyledSelect
          name="gender"
          value={internalFormState?.gender || ''}
          onChange={onInputValueChange}
        >
          <option value=""></option>
          <option value="Female">Female</option>
          <option value="Male">Male</option>
          <option value="Genderless">Genderless</option>
          <option value="unknown">unknown</option>
        </StyledSelect>
      </StyledLabel>
    </StyledForm>
  )
}
