export enum ActionTypes {
  START_FETCHING = 'START_FETCHING',
  FETCHING_SUCCEEDED = 'FETCHING_SUCCEEDED',
  FETCHING_RETURN_EMPTY = 'FETCHING_RETURN_EMPTY',
  FETCHING_FAILED = 'FETCHING_FAILED',
}

export enum LoadingState {
  IDLE = 'IDLE',
  PENDING = 'PENDING',
  SUCCEEDED = 'SUCCEEDED',
  FAILED = 'FAILED',
}

export const initialValue = {
  info: {
    count: 0,
    pages: 0,
  },
  results: [],
  error: null,
  loadingState: LoadingState.IDLE,
}

export const reducer = (
  state: typeof initialValue,
  action: { type: ActionTypes; [key: string]: any }
) => {
  switch (action.type) {
    case ActionTypes.FETCHING_SUCCEEDED:
      return {
        ...state,
        info: action.payload.info,
        results: action.payload.results,
        error: null,
        loadingState: LoadingState.SUCCEEDED,
      }

    case ActionTypes.FETCHING_RETURN_EMPTY:
      return initialValue

    case ActionTypes.START_FETCHING:
      return {
        ...state,
        loadingState: LoadingState.PENDING,
      }

    case ActionTypes.FETCHING_FAILED:
      return {
        ...initialValue, //old results and info are not valid
        loadingState: LoadingState.FAILED,
        error: action.payload,
      }

    default:
      return state
  }
}
