import {
  StyledCard,
  StyledCharactersProp,
} from '../StyledElements/StyledElements'

type CardProps = {
  character: Character | null
}

export const Card: React.FC<CardProps> = ({ character }) => {
  if (!character) return null

  return (
    <StyledCard>
      <div>
        <img src={character.image} width="300px" />
      </div>
      <h3>{character.name}</h3>
      <div>
        <StyledCharactersProp>Specie: {character.species}</StyledCharactersProp>
        <StyledCharactersProp>Type: {character.type}</StyledCharactersProp>
        <StyledCharactersProp>Status: {character.status}</StyledCharactersProp>
        <StyledCharactersProp>Gender: {character.gender}</StyledCharactersProp>
      </div>
    </StyledCard>
  )
}
