import React, { useEffect, useState, useReducer } from 'react'

import { Paginator } from '../Paginator/Paginator'
import { Filter } from '../Filter/Filter'
import { Modal } from '../Modal/Modal'
import {
  GlobalStyle,
  Container,
  CharactersList,
  CharactersElement,
} from '../StyledElements/StyledElements'
import { PreloaderContainer } from '../PreloaderContainer/PreloaderContainer'
import { Card } from '../Card/Card'
import { ActionTypes, LoadingState, initialValue, reducer } from '../App/utils'

const baseCharacterUrl = 'https://rickandmortyapi.com/api/character'

export const App = () => {
  const [charactersRequestParams, setCharactersRequestParams] =
    useState<RequestParams>({
      page: 1,
    })

  const [modalState, setModalState] = useState<{
    isOpen: boolean
    character: Character | null
  }>({
    isOpen: false,
    character: null,
  })

  const [charactersState, charactersDispatch] = useReducer(
    reducer,
    initialValue
  )

  const onPageClick = (page: number) => {
    setCharactersRequestParams((old) => ({
      ...old,
      page,
    }))
  }

  useEffect(() => {
    let requestCancelled = false

    const createURL = () => {
      const url = new URL(baseCharacterUrl)
      for (const [key, value] of Object.entries(charactersRequestParams)) {
        const valString = String(value)
        if (valString.length) {
          url.searchParams.set(key, String(value))
        }
      }

      return url as unknown as RequestInfo
    }

    const fetchCharacters = async () => {
      try {
        charactersDispatch({
          type: ActionTypes.START_FETCHING,
        })

        const response = await fetch(createURL())

        if (response.ok) {
          const data = await response.json()
          if (!requestCancelled) {
            charactersDispatch({
              type: ActionTypes.FETCHING_SUCCEEDED,
              payload: data,
            })
          }
        } else {
          if (!requestCancelled) {
            charactersDispatch({
              type: ActionTypes.FETCHING_RETURN_EMPTY,
            })
          }
        }
      } catch (error) {
        if (!requestCancelled) {
          charactersDispatch({
            type: ActionTypes.FETCHING_FAILED,
            payload: error,
          })
        }
      }
    }

    fetchCharacters()

    return () => {
      requestCancelled = true
    }
  }, [charactersRequestParams, charactersDispatch])
  const characterOnClick = (
    event: React.MouseEvent<HTMLElement>,
    character: Character
  ) => {
    setTimeout(() => {
      if (!window.getSelection()?.toString() || event.target instanceof Image) {
        setModalState({
          isOpen: true,
          character,
        })
      }
    }, 0)
  }

  return (
    <>
      <GlobalStyle />
      <Container>
        <PreloaderContainer
          isLoading={charactersState.loadingState === LoadingState.PENDING}
        >
          <h1>Rick and Morty</h1>
          <Filter setFilterParams={setCharactersRequestParams} />
          {(charactersState.loadingState === LoadingState.SUCCEEDED ||
            charactersState.loadingState === LoadingState.PENDING) &&
            charactersState.results.length > 0 && (
              <CharactersList>
                {charactersState.results.map((character: Character) => (
                  <CharactersElement
                    key={character.id}
                    onClick={(event: React.MouseEvent<HTMLElement>) =>
                      characterOnClick(event, character)
                    }
                  >
                    <Card character={character} />
                  </CharactersElement>
                ))}
              </CharactersList>
            )}
          {charactersState.loadingState === LoadingState.IDLE && (
            <div>
                .    
               
            </div>
          )}

          {charactersState.loadingState === LoadingState.FAILED && (
            <div>   : {String(charactersState.error)}</div>
          )}

          <Paginator
            pages={charactersState.info.pages}
            current={charactersRequestParams.page}
            onClick={onPageClick}
          />
        </PreloaderContainer>
      </Container>
      <Modal
        isOpen={modalState.isOpen}
        onClose={() =>
          setModalState({
            isOpen: false,
            character: null,
          })
        }
      >
        <Card character={modalState.character} />
      </Modal>
    </>
  )
}
