import {
  StyledPreloaderContainer,
  Overlay,
  Spinner,
} from '../StyledElements/StyledElements'

type PreloaderContainerProps = {
  isLoading: boolean
}

export const PreloaderContainer: React.FC<PreloaderContainerProps> = ({
  isLoading = false,
  children,
}) => {
  return (
    <StyledPreloaderContainer>
      {isLoading && (
        <>
          <Overlay />
          <Spinner />
        </>
      )}

      {children}
    </StyledPreloaderContainer>
  )
}
